import { load as loadYAML, dump as dumpYAML } from "js-yaml";
import { Zstd } from "@hpcc-js/wasm-zstd";
const zstd = await Zstd.load();
// Base64 → Uint8Array（URLセーフに対応）
function base64ToUint8Array(base64) {
    // パディングの補完
    let paddedBase = base64.replace(/-/g, '+').replace(/_/g, '/');
    const padding = paddedBase.length % 4;
    if (padding === 2)
        paddedBase += '==';
    else if (padding === 3)
        paddedBase += '=';
    const binaryString = atob(paddedBase);
    const len = binaryString.length;
    const bytes = new Uint8Array(len);
    for (let i = 0; i < len; i++) {
        bytes[i] = binaryString.charCodeAt(i);
    }
    return bytes;
}
// Uint8Array → Base64（URLセーフ対応）
function uint8ArrayToBase64(bytes) {
    let binary = '';
    for (let i = 0; i < bytes.length; i++) {
        binary += String.fromCharCode(bytes[i]);
    }
    let base64 = btoa(binary);
    // URLセーフ変換
    base64 = base64.replace(/\+/g, '-').replace(/\//g, '_').replace(/=+$/, '');
    return base64;
}
// zstdで展開
function zstdDecompress(compressed) {
    try {
        // zstd.decompress() で zstd データを展開
        const decompressedData = zstd.decompress(compressed);
        return new TextDecoder('utf-8').decode(decompressedData);
    }
    catch (err) {
        console.error("展開エラー:", err);
        return null;
    }
}
// zstdで圧縮
function zstdCompress(text) {
    try {
        // 文字列をUTF-8のUint8Arrayに変換
        const input = new TextEncoder().encode(text);
        // zstd.compress() でzstd圧縮
        return zstd.compress(input);
    }
    catch (err) {
        console.error("圧縮エラー:", err);
        return null;
    }
}
// Decode => 展開 => YAML load
function decodeProcess(encodedData) {
    let dataObject = null;
    try {
        // デコード => 圧縮データ
        const compressedData = base64ToUint8Array(encodedData);
        // zstdで展開
        const yamlData = zstdDecompress(compressedData);
        if (yamlData) {
            // YAML文字列 => JavaScriptオブジェクト
            dataObject = loadYAML(yamlData);
        }
    }
    catch (error) {
        console.error("エラーが発生しました:", error);
    }
    return dataObject;
}
// YAML dump => 圧縮 => Encode
function encodeProcess(dataObject) {
    let encodedData = null;
    try {
        // YAMLオブジェクト => YAML文字列
        const yamlData = dumpYAML(dataObject);
        // zstdで圧縮
        const compressedData = zstdCompress(yamlData);
        if (compressedData) {
            // 圧縮データ => Base64
            encodedData = uint8ArrayToBase64(compressedData);
        }
    }
    catch (error) {
        console.error("エラーが発生しました:", error);
    }
    return encodedData;
}
async function fetchSearchSkill(seachUrls) {
    try {
        // URLごとにリクエストを作成
        const requests = seachUrls.map(url => fetch(url));
        // すべてのリクエストが完了するまで待機
        const responses = await Promise.all(requests);
        // 各レスポンスごとに処理を実行
        const data = await Promise.all(responses.map(async (response, idx) => {
            if (!response.ok) {
                throw new Error(`HTTP error! status: ${response.status}`);
            }
            const jsonData = await response.json();
            // URLごとに異なる処理を追加する場合はここに追加
            //console.debug(`Data from URL ${idx}:`, jsonData);
            const skillLvElement = document.getElementById("OBJID_SELECT_LEARNED_SKILL_LEVEL_" + jsonData.ratorio_skill_num);
            skillLvElement.setAttribute("data-skill-name", jsonData.skill_name);
        }));
    }
    catch (error) {
        console.error('Error fetching data:', error);
    }
}
export async function loadRodbTranslator(fragment) {
    const supportVersion = 2;
    const prefixCheck = /^rtx(\d+):(.+)$/;
    const matches = prefixCheck.exec(fragment);
    if (!matches) {
        return;
    }
    // Version Check
    if (Number(matches[1]) != supportVersion) {
        alert("RODB Translatorから出力された\nフォーマットバージョンが異なるため中止します\nVersion:" + matches[1]);
        return;
    }
    // フラグメントをデコード
    const decodedData = decodeURIComponent(matches[2]);
    // 中身のデコード、zstd展開を行う
    const yamlObject = decodeProcess(decodedData);
    if (!yamlObject) {
        alert("URLからのデータロードに失敗しました");
        return;
    }
    // Set Job
    const jobElement = document.getElementById("OBJID_SELECT_JOB");
    jobElement.value = yamlObject.status.job_class.toLocaleUpperCase();
    changeJobSettings(yamlObject.status.job_class.toLocaleUpperCase());
    // Set Base Lv
    const baseLvElement = document.getElementById("OBJID_SELECT_BASE_LEVEL");
    baseLvElement.value = String(yamlObject.status.base_lv);
    // Set Job Lv
    const jobLvElement = document.getElementById("OBJID_SELECT_JOB_LEVEL");
    jobLvElement.value = String(yamlObject.status.job_lv);
    // Set status
    const keys = [
        "str", "agi", "vit", "int", "dex", "luk",
        "pow", "sta", "wis", "spl", "con", "crt"
    ];
    for (const key of keys) {
        const statusElement = document.getElementById("OBJID_SELECT_STATUS_" + key.toUpperCase());
        let value = yamlObject.status[key];
        statusElement.value = String(value);
    }
    // Set Skill Lv
    const skillColumnCheckbox = document.getElementById("OBJID_SKILL_COLUMN_EXTRACT_CHECKBOX");
    skillColumnCheckbox.checked = true;
    OnClickSkillSWLearned();
    let seachUrls = [];
    const urlPrefix = "https://ro-database.info/translator/approximate_search/skill";
    let idx = 0;
    while (true) {
        const skillNameElement = document.getElementById("OBJID_TD_LEARNED_SKILL_NAME_" + idx);
        if (!skillNameElement) {
            break;
        }
        const skillName = skillNameElement.textContent?.trim();
        if (skillName) {
            seachUrls.push(`${urlPrefix}?word=${encodeURIComponent(skillName)}&ratorio_skill_num=${idx}`);
        }
        idx++;
    }
    // スキルのSelectBoxにdata-skill-name属性を付与
    await fetchSearchSkill(seachUrls);
    Object.entries(yamlObject.skills).forEach(([skillName, skill]) => {
        const skillLvElement = document.querySelector(`select[data-skill-name=${skillName}]`);
        console.debug(`${skillName}`);
        if (skillLvElement) {
            skillLvElement.value = String(skill.lv);
            console.debug(`${skillName} : ${skillLvElement.value}`);
            const event = new Event('change', { bubbles: true });
            skillLvElement.dispatchEvent(event);
        }
    });
    // 計算
    CalcStatusPoint(true);
    StAllCalc();
    AutoCalc();
}
